@import url('app.css');

.navbar {
  padding: 10px 20px;
  background-color: var(--navy)
}

.navlink {
  color: var(--off-white);
}

.navlink:hover {
  color: var(--light-peach);
}

.navlink > a {
  color: inherit;
  text-decoration: none;
}

.navlink > a:hover{
  color: inherit;
  text-decoration: none;
}