.about-container {
  margin-top: 30px;
  display: grid;
  grid-template-columns: auto auto auto;
  margin-left: 6%;
  margin-bottom: 50px;
}

.about-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--navy);
  border-bottom: 3px solid var(--light-peach);
  padding-bottom: 4px;
}
