@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Recursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --navy: #173e43;
  --light-blue: #3fb0ac;
  --light-peach: #fae596;
  --grey: #484848;
  --off-white: #ededed;
}

html {
  scroll-behavior: smooth;
}

h {
  color: #1b2d45;
  font-size: 1.9rem;
  font-weight: 700;
  margin: 30px;
  padding-top: 7%;
  font-family: 'Noto Sans TC';
}

h::after, h::before {
  color: #3fb0ac;
  color: var(--light-blue);
  font-family: 'Source Code Pro', monospace;
  font-size: 2rem;
}

#body {
  margin: auto;
  color: #242424;
  /* border: 1px solid green; */
  max-width: 1250px;
  width: 80%;
}

.navbar {
  padding: 10px 20px;
}

.navlink {
  color: #ededed;
  color: var(--off-white);
}

.navlink:hover {
  color: #fae596;
  color: var(--light-peach);
}

.navlink > a {
  color: inherit;
  text-decoration: none;
}

.navlink > a:hover{
  color: inherit;
  text-decoration: none;
}

.header {
  background-color: #173e43;
  background-color: var(--navy);
}

.footer {
  background-color: #173e43;
  background-color: var(--navy);
  margin-top: 50px;
  padding: 1rem;
}

.contacts {
  max-width: 200px;
  justify-content: space-around;
  margin: auto;
  font-size: 2.5rem;
}

.contact {
  color: #ededed;
  color: var(--off-white);
}

#copyright {
  color: #ededed;
  color: var(--off-white);
  margin: auto;
}

.contact:hover {
  color: #fae596;
  color: var(--light-peach);
}

#copyright::before {
  content: '\00A9';
  margin-right: 3px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  width: 100%;
  padding: 0 10%;
}

h1 {
  padding: 0 20px;
  font-size: 2rem;
}

p {
  padding: 0 20px;
}

.home-container {
  padding: 7% 0%;
}

@media (max-height: 800px) and (min-width: 1300px) {
  .home-container {
    min-height: 100vh;
  }
}
.timeline-container {
  margin-top: 30px;
  margin-left: 6%;
  margin-bottom: 50px;
}

.timeline {
  border-left: 4px solid var(--light-blue);
  padding-top: 0.2em;
  margin: 9px;
}

.circle {
  background-color: #fff;
  border: 3px solid var(--light-blue);
  border-radius: 50%;
  position: absolute;
  left: -42px;
  top: 20%;
  width: 20px;
  height: 20px;
  z-index: 10;
}

.timeline-item {
  padding: 3%;
  padding-left: 30px;
  max-width: 800px;
}

.title-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.timeline-item > h5 {
  color: var(--grey);
  font-size: 1em;
}

.title-container > h4 {
  color: var(--navy);
}

.logo {
  width: 40px;
  padding: 3px;
  margin-left: 5px;
  margin-bottom: 15px;
}

.timeline-item-content {
  margin: 2px 3%;
}

.toggle {
  border: none;
  background: inherit;
  padding: 5px 0;
  color: var(--light-peach);
}

.toggle:hover {
  color: var(--peach);
}

.toggle::after {
  content: ' \25BC';
  font-size: 12px;
}

.toggle:focus {
  outline: none;
}
.navbar {
  padding: 10px 20px;
  background-color: var(--navy)
}

.navlink {
  color: var(--off-white);
}

.navlink:hover {
  color: var(--light-peach);
}

.navlink > a {
  color: inherit;
  text-decoration: none;
}

.navlink > a:hover{
  color: inherit;
  text-decoration: none;
}
.about-container {
  margin-top: 30px;
  display: grid;
  grid-template-columns: auto auto auto;
  margin-left: 6%;
  margin-bottom: 50px;
}

.about-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--navy);
  border-bottom: 3px solid var(--light-peach);
  padding-bottom: 4px;
}

.writing-container {
    flex-wrap: wrap;
    margin-bottom: 30px;
    margin-left: 6%;
    margin-top: 30px;
    width: 900px;
  }
  
  .card-container {
    margin: 15px;
    box-shadow: 0 4px 6px -1px rgba(0,0,0,.1),0 2px 0 -1px rgba(0,0,0,.06);
    width: 400px;
    height: 380px;
  }
  
  .card-title {
    color: var(--navy);
    padding-bottom: 3.5px;
    border-bottom: 2.2px solid var(--light-peach);
    font-weight: 600;
  }
  
  .card-subtitle {
    color: var(--grey);
    margin-bottom: 5px;
  }
  
  .card-text {
    height: 50px;
  }
  
  .card-img {
    object-fit: fill;
    height: 200px;
    padding: 0;
  }
  
  .button, .button:focus {
    float: right;
    text-align: center;
    padding: 0.2rem 0.5rem;
    background-color: var(--light-blue);
    border: 2px solid var(--light-blue);
    color: white;
    outline: none;
  }
  
  .button:hover {
    cursor: pointer;
    background-color: white;
    color: var(--light-blue);
    border-color: var(--light-blue);
  }
  
  .modal .button {
    margin-right: 40px;
    margin-bottom: 20px;
  }
  
  .proj-img {
    padding: 0;
    margin-bottom: 1rem;
  }
  
  .comment {
    font-style: italic;
    font-weight: 200;
    margin-top: 30px;
  }

  .button > a {
    color: inherit;
    text-decoration: none;
  }
  
  .button > a:hover{
    color: inherit;
    text-decoration: none;
  }
