@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC&display=swap');
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:900&display=swap');

:root {
  --navy: #173e43;
  --light-blue: #3fb0ac;
  --light-peach: #fae596;
  --grey: #484848;
  --off-white: #ededed;
}

html {
  scroll-behavior: smooth;
}

h {
  color: #1b2d45;
  font-size: 1.9rem;
  font-weight: 700;
  margin: 30px;
  padding-top: 7%;
  font-family: 'Noto Sans TC';
}

h::after, h::before {
  color: var(--light-blue);
  font-family: 'Source Code Pro', monospace;
  font-size: 2rem;
}

#body {
  margin: auto;
  color: #242424;
  /* border: 1px solid green; */
  max-width: 1250px;
  width: 80%;
}

.navbar {
  padding: 10px 20px;
}

.navlink {
  color: var(--off-white);
}

.navlink:hover {
  color: var(--light-peach);
}

.navlink > a {
  color: inherit;
  text-decoration: none;
}

.navlink > a:hover{
  color: inherit;
  text-decoration: none;
}

.header {
  background-color: var(--navy);
}

.footer {
  background-color: var(--navy);
  margin-top: 50px;
  padding: 1rem;
}

.contacts {
  max-width: 200px;
  justify-content: space-around;
  margin: auto;
  font-size: 2.5rem;
}

.contact {
  color: var(--off-white);
}

#copyright {
  color: var(--off-white);
  margin: auto;
}

.contact:hover {
  color: var(--light-peach);
}

#copyright::before {
  content: '\00A9';
  margin-right: 3px;
}